import Vue from 'vue'
import Router from 'vue-router'
import TopicList from '@/components/TopicList'
import Login from '@/components/Login'
import Center from '@/components/Center'
import Msg from '@/components/Msg'
import Logout from '@/components/Logout'
import Topic from '@/components/Topic'
import User from '@/components/User'
import Create from '../components/Create.vue'
import Edit from '../components/Edit.vue'
Vue.use(Router)
export default new Router({
  routes: [
    {path:'/',redirect:'/topiclist/all'},
    {path: '/topiclist/:name', component: TopicList,props:true,name:'topicList'  },//props
    {path:'/topic/:id',component:Topic,props:true},
    {path:'/user/:loginname',component:User,props:true},
    {path:'/login',component:Login},
    {path:'/center',component:Center},
    {path:'/msg',component:Msg},
    {path: '/logout', component: Logout },
    { path: '/create', name: 'create', component: Create },
    // 
    {
      path: '/topic/:id/edit', name: 'edit', component: Edit, props:true
    }
    
    
  ]
})



