
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:'User',
components:{
Card,Group,Cell
},
props:['loginname'],
data:function(){
return{
userinfo:'',
collect_topics:[]
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){

vm.$http.get('/user/'+to.params.loginname)
.then(function(res){
console.log(res.data);
vm.userinfo=res.data.data;
})

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(res){
vm.collect_topics=res.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){
var app=this;
this.$http.get('/user/'+to.params.loginname)
.then(function(res){
console.log(res.data);
app.userinfo=res.data.data;
})

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(res){
app.collect_topics=res.data.data
})
next();
}
}



