
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default{
name:'create',
data() {
return {
title:'',//
content:'',//
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 





ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code

fullscreen: true, // 

htmlcode: true, // html
help: true, // 
/* 1.3.5 */




/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */

preview: true, // 
}
}
},
components:{
Group,
XTextarea,
XButton,
mavonEditor
},
methods: {

addtopic:function(){

var app = this;

this.$http.post('/topics',{
title:this.title,
content:this.content,
tab:'dev',
accesstoken:this.$store.state.accesstoken
}).then(function(res){

if(res.data &&res.data.success){
app.$router.replace('/topiclist/dev');
}
})
}
},
}



